/* ======================================
 * ФОРМЫ (forms)
/* ====================================== */

.form-block {
  max-width: 400px;
  margin: 0.5em auto;
  padding: 0.5em 0 1em;
  border: 1px solid rgba(0, 0, 0, 0.2);
  background: rgba(0, 0, 0, 0.5);
}

.form-block h2 {
  padding: 0.5em 0;
  text-align: center;
  color: white;
}

.form-block h2 span {
  color: red;
}

.form-block .form-vertical {
  display: flex;
  flex-flow: column wrap;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.form-block .form-vertical fieldset {
  padding: .5em 0;
}

.form-block .form-vertical label {
  display: flex;
  flex-flow: row wrap;
  justify-content: ;
  align-items: center;
  margin: 0;
  padding: 0;
  text-align: left;
  color: white;
}

.form-block .form-vertical label input[type='checkbox'] {
  margin: 0.5em;
  padding: 0;
  font-size: 1em;
  transform: scale(1.5);
}

.form-block .form-vertical label span {
  margin: 0;
  padding: 0;
}

.form-block .form-vertical input[type='text'],
.form-block .form-vertical input[type='tel'] {
  max-width: 350px;
  width: 90%;
  height: auto;
  margin: 0.5em auto;
  padding: 1em;
  box-sizing: border-box;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 3px;
  box-shadow: 0 1px 2px -1px rgba(0, 0, 0, 0.2) inset, 0 0 transparent;
  resize: none;
}

.form-block .form-vertical input[type='text']:hover,
.form-block .form-vertical input[type='tel']:hover {
  border-color: #7eb4ea;
  box-shadow: 0 1px 2px -1px rgba(0, 0, 0, 0.2) inset, 0 0 transparent;
}

.form-block .form-vertical input[type='text']:focus,
.form-block .form-vertical input[type='tel']:focus {
  outline: none;
  border-color: #7eb4ea;
  box-shadow: 0 1px 2px -1px rgba(0, 0, 0, 0.2) inset, 0 0 4px rgba(35, 146, 243, 0.5);
  transition: .2s linear;
}

.form-block .form-vertical input[type='submit'] {
  max-width: 350px;
  width: 90%;
  height: auto;
  margin: 0.5em auto;
  padding: 0.75em;
  font-size: 1.1em;
  font-weight: bold;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 3px;
  background: rgba(92, 144, 194, 0.8);
  box-shadow: 0 1px 2px -1px rgba(0, 0, 0, 0.2) inset, 0 0 transparent;
  transition: background 0.5s ease, color 0.3s ease;
  color: white;
}

.form-block .form-vertical input[type='submit']:disabled {
  background: rgba(92, 144, 194, 0.3);
  color: silver;
  cursor: default;
  transform: none;
  transition: none;
  box-shadow: none;
}

.form-block .form-vertical input[type='submit']:disabled:hover {
  cursor: default;
  background: rgba(92, 144, 194, 0.3);
  transform: none;
  transition: none;
  box-shadow: none;
}

.form-block .form-vertical input[type='submit']:hover {
  cursor: pointer;
  background: #4169e1;
}

.form-block .form-vertical input[type='submit']:focus {
  box-shadow: 0 0 0.5em 0.5em rgba(65, 105, 225, 0.8);
}

.form-block .form-vertical input[type='submit']:active {
  transform: translateY(2px);
  box-shadow: 0 0 0.5em 0.5em rgba(65, 105, 225, 0.8);
}

.form-block .form-modal {
  display: inline-block;
  max-width: 400px;
  min-width: 280px;
  box-sizing: border-box;
  left: 50%;
  opacity: 0;
  position: fixed;
  text-align: justify;
  top: 40%;
  visibility: hidden;
  z-index: 999999;
  transform: translate(-50%, -50%);
  transition: opacity .5s, top .5s;
  background: #ffffff;
}

.form-block .form-modal .close-modal {
  position: absolute;
  padding: 1px 9px 4px 9px;
  top: -15px;
  right: -15px;
  cursor: pointer;
  color: #fff;
  font-family: 'tahoma', sans-serif;
  background: #ee7156;
  text-align: center;
}

.form-block .form-modal h5 {
  width: 100%;
  margin: 0;
  padding: 0.5em 0;
  font-size: 18px;
  font-weight: normal;
  text-align: center;
  color: white;
  text-transform: uppercase;
  background: #72bffc;
}

.form-block .form-modal p {
  padding: 1em;
  font-size: 1em;
  text-transform: none;
  text-align: center;
  font-weight: 400;
  color: rgba(0, 0, 0, 0.8);
}

.form-block .form-modal hr {
  background: #63baff;
  height: 1px;
  border: none;
}

.form-block .form-overlay {
  background-color: rgba(0, 0, 0, 0.7);
  bottom: 0;
  cursor: default;
  left: 0;
  opacity: 0;
  position: fixed;
  right: 0;
  top: 0;
  visibility: hidden;
  z-index: 99999;
  transition: opacity .5s;
}

.order-modal {
  max-width: 350px;
  width: 280px;
}

.order-modal .form {
  width: 90%;
  margin: 0 auto;
}

.open:hover {
  cursor: pointer;
  color: red;
}

#open-window,
.modal-window {
  display: none;
}

#open-window:checked + .modal-window {
  background: rgba(0, 0, 0, 0.5);
  display: block;
  width: 100%;
  height: 100%;
  position: fixed;
  left: 0;
  top: 0;
  overflow-y: scroll;
  z-index: 1002;
}

.window-container {
  width: 80%;
  max-width: 700px;
  margin: 15% auto;
  padding-bottom: 0.5em;
  text-align: justify;
  background: white;
  overflow-y: auto;
  /* прокрутка по вертикали */
}

.window-container h2 {
  position: relative;
  margin: 0;
  padding: 0.5em;
  font-size: 100%;
  text-align: center;
  color: white;
  background: #4d83c0;
}

.window-container p {
  margin: 0.5em 1em;
  line-height: 145%;
  color: black;
}

.close-window span {
  position: absolute;
  top: 0;
  right: 0;
  width: 30px;
  height: 30px;
  padding: 0;
  margin: 0;
  border: 0;
  border-radius: 0;
  outline: none;
  background: transparent;
  z-index: 13;
  cursor: pointer;
}

.close-window span::after {
  content: "×";
  position: absolute;
  top: 2px;
  right: 2px;
  width: 30px;
  height: 30px;
  font: 20px/30px Arial, Helvetica Neue, Helvetica, sans-serif;
  color: #888;
  font-weight: 300;
  text-align: center;
  border-radius: 50%;
  border-width: 0;
  background: #fff;
  transition: background .2s;
  box-sizing: border-box;
  z-index: 5;
  cursor: pointer;
}

.close-window span:focus::after {
  outline: 1px dotted #888;
}

.close-window span:hover::after {
  color: #555;
  background: #eee;
}

.close-window:hover {
  color: white;
  cursor: pointer;
}

