/* Маленькая кнопка */

.btn {
  display: inline-block;
  width: auto;
  margin: .5em 0;
  padding: .3em 1em;
  color: #fff;
  background: rgba(65, 105, 225, 0.6);
  -webkit-transition: background 0.5s ease, color 0.3s ease;
  transition: background 0.5s ease, color 0.3s ease;
  border: none;
  font-size: .9em;
}

.btn:hover {
  background: #4169e1;
  color: #fff;
  cursor: pointer;
}

.btn:active,
.btn .is-checked {
  box-shadow: 0 0 40px rgba(0, 0, 0, 0.5) inset;
  background: #4169e1;
}

.btn .is-checked {
  color: white;
}

.btn-group:after {
  content: '';
  display: block;
  clear: both;
}

/* Средняя кнопка */

.btn-middle {
  display: block;
  margin: .5em auto;
  padding: 1em;
  color: #fff;
  background: rgba(65, 105, 225, 0.6);
  -webkit-transition: background 0.5s ease, color 0.3s ease;
  transition: background 0.5s ease, color 0.3s ease;
  border: none;
  font-size: 1em;
}

.btn-middle:hover {
  background: #4169e1;
  color: #fff;
  cursor: pointer;
}

.btn-middle:active,
.btn-middle .is-checked {
  background: #4169e1;
}

.btn-middle .is-checked {
  color: white;
}

.btn-middle:active {
  box-shadow: 0 0 40px rgba(0, 0, 0, 0.5) inset;
}

.btn-middle-group:after {
  content: '';
  display: block;
  clear: both;
}

/* Кнопка быстрого заказа (header) */

.quickOrderButton {
  display: inline-block;
  animation: cycle 6s linear infinite;
  -webkit-animation: cycle 6s linear infinite;
}

@-webkit-keyframes cycle {
  35% {
    -webkit-transform: rotate(0) translate(0, 0);
    transform: rotate(0) translate(0, 0);
  }

  40% {
    -webkit-transform: rotate(5deg) translate(0, -2px);
    transform: rotate(5deg) translate(0, -2px);
  }

  45% {
    -webkit-transform: rotate(-3deg) translate(0, -2px);
    transform: rotate(-3deg) translate(0, -2px);
  }

  50% {
    -webkit-transform: rotate(5deg) translate(0, -2px);
    transform: rotate(5deg) translate(0, -2px);
  }

  55% {
    -webkit-transform: rotate(-3deg) translate(0, -2px);
    transform: rotate(-3deg) translate(0, -2px);
  }

  60% {
    -webkit-transform: rotate(5deg) translate(0, -2px);
    transform: rotate(5deg) translate(0, -2px);
  }

  65% {
    -webkit-transform: rotate(-3deg) translate(0, -2px);
    transform: rotate(-3deg) translate(0, -2px);
  }

  70% {
    -webkit-transform: rotate(0) translate(0, 0);
    transform: rotate(0) translate(0, 0);
  }
}

