/* ======================================
 * ОБЩИЕ СТИЛИ
/* ====================================== */

html {
  font-size: 1em;
}

body {
  font-family: Verdana;
  line-height: 165%;
  font-weight: 400;
  box-sizing: border-box;
  border-collapse: collapse;
  color: rgba(0, 0, 0, 0.8);
}

.overlay:before {
  content: '';
  display: block;
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background: rgba(0, 0, 0, 0.5);
  z-index: 100;
}

.preloader:after {
  content: '';
  display: block;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 55px;
  width: 55px;
  margin: auto;
  background: white url("../img/icons/preloader.gif") no-repeat center center;
  z-index: 101;
  border-radius: 6px;
  -moz-border-radius: 6px;
  -webkit-border-radius: 6px;
  -o-border-radius: 6px;
  -ms-border-radius: 6px;
}

:-ms-input-placeholder {
  color: #757575;
}

:-webkit-input-placeholder {
  color: #757575;
}

/* headings */

h2,
h3,
h4,
h5,
h6 {
  padding: 1em 0;
}

h2 {
  font-size: 1.3em;
}

h3 {
  font-size: 1.2em;
}

h4 {
  font-size: 1em;
}

h5 {
  font-size: 0.9em;
}

h6 {
  font-size: 0.8em;
}

p,
th,
td {
  -moz-hyphens: auto;
  -webkit-hyphens: auto;
  -ms-hyphens: auto;
}

/* Черта */

hr {
  display: block;
  margin: 0.5em auto;
  padding: 0;
  border: 0;
  height: 1px;
  background-image: -webkit-linear-gradient(left, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.75), rgba(0, 0, 0, 0));
  background-image: -o-linear-gradient(left, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.75), rgba(0, 0, 0, 0));
}

.hr-long {
  width: auto;
  border: 1px solid rgba(0, 0, 0, 0.1);
}

.hr-fat {
  width: auto;
  border-bottom: 3px solid rgba(255, 255, 255, 0.5);
}

