header .top-line {
  display: flex;
  flex-wrap: wrap;
  margin-left: -1.15%;
  margin-right: -1.15%;
  justify-content: space-around;
  align-items: center;
  padding: 2em 0;
}

header .top-line .logotip,
header .top-line .activity,
header .top-line .contacts {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 1.15%;
  margin-right: 1.15%;
  margin: 0;
  color: white;
}

header .top-line .activity {
  text-align: center;
}

header .top-line .contacts {
  text-align: right;
}

header .top-line .contacts .tel {
  font-size: 1.2em;
  font-weight: 700;
}

header .top-line .social {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 1.15%;
  margin-right: 1.15%;
  margin: 0;
}

header .top-line .social a {
  text-decoration: none;
}

header .top-line .social img {
  margin: 0 0.5em;
  background: #ffffff;
  border-radius: 50%;
  -moz-border-radius: 50%;
  -webkit-border-radius: 50%;
  -o-border-radius: 50%;
  -ms-border-radius: 50%;
}

header .top-line .social img:hover {
  cursor: pointer;
  transform: scale(1.2);
  transition: all 1s linear;
}

header .promo {
  padding: 2em 0;
}

header .promo h1 {
  padding: 0.5em 0;
  text-align: center;
  font-size: 1.7em;
  font-weight: bold;
  line-height: 145%;
  color: white;
  text-transform: uppercase;
}

header .promo p {
  text-align: center;
  font-weight: bold;
  font-size: 1.5em;
  color: red;
  text-transform: uppercase;
}

header .promo .shadow {
  color: #ffffff;
  background: rgba(0, 0, 0, 0.4);
}

header .promo .promo-content {
  padding: 7em 0 2em;
}

/* ======================================
 * ПРЕИМУЩЕСТВА (advantages)
/* ====================================== */

.advantages {
  padding: 3em 0;
  background: #ffffff;
  /* Блок "Мини-галерея" */
}

.advantages .items {
  display: flex;
  flex-wrap: wrap;
  margin-left: -1.15%;
  margin-right: -1.15%;
  justify-content: space-around;
  padding: 1.5em 0;
}

.advantages .items .item {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 1.15%;
  margin-right: 1.15%;
  width: 31.03333333%;
  display: flex;
  flex-direction: column;
  margin: 0.5em;
  padding: 0.5em 0;
  text-align: center;
  transition: width 1s;
}

.advantages .items .item img {
  margin: 0.5em auto;
}

.advantages .items .item .item-text {
  margin: 1em auto;
}

.advantages .items .item .item-text p {
  font-size: .9em;
}

.advantages .items .item .item-text p span {
  font-weight: bold;
}

/* ======================================
 * ОЦЕНКА АВТО (diagnostics)
/* ====================================== */

.diagnostics {
  padding: 3em 0;
  text-align: center;
  background: url("../img/bg/car-diagnostics-bg.jpg") 100% 100% no-repeat;
  background-attachment: fixed;
  background-size: cover;
  box-shadow: 0 0 0 50em rgba(0, 0, 0, 0.33) inset;
}

.diagnostics h2 {
  padding: 1em 0;
  color: white;
}

.diagnostics .items {
  display: flex;
  flex-wrap: wrap;
  margin-left: -1.15%;
  margin-right: -1.15%;
  justify-content: center;
  align-items: center;
  margin: 1.5em 0;
}

.diagnostics .items .item {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 1.15%;
  margin-right: 1.15%;
  width: 47.7%;
}

.diagnostics .items .item .item-img {
  margin: 1em 0;
}

.diagnostics .items .item .item-img img {
  border-radius: 338px;
  -moz-border-radius: 338px;
  -webkit-border-radius: 338px;
  -o-border-radius: 338px;
  -ms-border-radius: 338px;
}

.diagnostics .items .item .item-text {
  margin: 0 auto;
  padding: 1em 0;
  color: white;
  background: rgba(0, 0, 0, 0.3);
}

.diagnostics .items .item .item-text .list {
  text-align: left;
}

/* ======================================
 * СХЕМА РАБОТЫ (order-process)
/* ====================================== */

.order-process {
  padding: 3em 0;
  background: #ffffff;
  /* Блок "Мини-галерея" */
}

.order-process p {
  text-align: center;
  font-weight: bold;
}

.order-process .items {
  display: flex;
  flex-wrap: wrap;
  margin-left: -1.15%;
  margin-right: -1.15%;
  justify-content: space-around;
  padding: 1.5em 0;
}

.order-process .items .item {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 1.15%;
  margin-right: 1.15%;
  width: 14.36666667%;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0.5em;
  padding: 0.5em 0;
  text-align: center;
  transition: width 1s;
}

.order-process .items .item .item-header {
  width: 5em;
  height: 5em;
  line-height: 5em;
  padding: 0;
  font-size: 1.2em;
  font-weight: bold;
  text-align: center;
  background-color: #42a5f6;
  border-radius: 50%;
  -moz-border-radius: 50%;
  -webkit-border-radius: 50%;
  -o-border-radius: 50%;
  -ms-border-radius: 50%;
  border: .125rem solid #fff;
  transition: all 1s ease;
  box-sizing: border-box;
}

.order-process .items .item .item-header span {
  font-size: 1.2em;
  color: white;
}

.order-process .items .item:hover .item-header {
  box-shadow: 0px 0px 10px rgba(0, 113, 188, 0.8), 0px 0px 15px rgba(0, 113, 188, 0.8);
  transition: all 1s ease;
}

.order-process .items .item:hover .item-header span {
  font-size: 1.5em;
  transition: all .5s ease;
}

.order-process .items .item figcaption {
  margin: 1em auto;
  font-size: .9em;
}

/* ======================================
 * ПОРТФОЛИО (examples)
/* ====================================== */

.examples {
  padding: 3em 0;
  background: url("../img/bg/examples-bg.jpg") 100% 100% no-repeat;
  background-attachment: fixed;
  background-size: cover;
  box-shadow: 0 0 0 30em rgba(0, 0, 0, 0.33) inset;
}

.examples h2 {
  padding: 1em 0;
  color: white;
}

.examples .items {
  display: flex;
  flex-wrap: wrap;
  margin-left: -1.15%;
  margin-right: -1.15%;
  justify-content: space-around;
  padding: 1.5em 0;
}

.examples .items .item {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 1.15%;
  margin-right: 1.15%;
  width: 31.03333333%;
  display: flex;
  flex-direction: column;
  margin: 0.5em;
  padding: 0;
  text-align: center;
  transition: width 1s;
  overflow: hidden;
  background: rgba(255, 255, 255, 0.9);
}

.examples .items .item .item-img {
  position: relative;
  width: 100%;
  height: 100%;
  margin: 0 auto;
}

.examples .items .item .item-img img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.examples .items .item .item-img span {
  display: inline-block;
  position: absolute;
  bottom: 1.7em;
  left: 0px;
  font-size: 90%;
  font-weight: bold;
  color: silver;
  padding: 0.3em 1.5em;
  background-color: rgba(255, 0, 0, 0.6);
}

.examples .items .item .item-text .properties {
  max-width: 100%;
  width: 100%;
  padding: 1em;
  font-size: 80%;
  text-align: center;
  border-spacing: 0;
  line-height: 146%;
  border-collapse: collapse;
  -moz-hyphens: auto;
  -webkit-hyphens: auto;
  -ms-hyphens: auto;
}

.examples .items .item .item-text .properties h3 {
  font-weight: bold;
  text-align: center;
  color: #45af2a;
}

.examples .items .item .item-text .properties tr {
  margin: .5em;
  text-align: left;
  box-shadow: 0 0 40px rgba(0, 0, 0, 0.1) inset;
}

.examples .items .item .item-text .properties td,
.examples .items .item .item-text .properties th {
  width: 50%;
  padding: .5em 1em;
  border: 1px solid rgba(0, 0, 0, 0.2);
}

.examples .items .item .item-text .properties .tr-hover:hover {
  box-shadow: 0 0 40px rgba(0, 0, 0, 0.2) inset;
}

.examples .items .item .item-text .properties .td-bg {
  color: #00b9ff;
}

.examples .items .item .item-text .properties .rate {
  font-weight: bold;
}

.examples .items .item .item-text .properties .price {
  color: #dc143c;
}

.examples .items .item .item-text .properties .price-color {
  color: red;
  font-size: 1.4em;
  font-weight: bold;
}

/* ======================================
 * ОТЗЫВЫ (reviews)
/* ====================================== */

.reviews {
  padding: 3em 0;
  box-sizing: border-box;
}

.reviews .items {
  display: flex;
  flex-wrap: wrap;
  margin-left: -1.15%;
  margin-right: -1.15%;
  padding: 1.5em 0;
  justify-content: center;
}

.reviews .items .item {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 1.15%;
  margin-right: 1.15%;
  width: 81.03333333%;
}

.reviews .items .item .accordion .accordion_item {
  margin-bottom: 1px;
  position: relative;
}

.reviews .items .item .accordion .title_block {
  padding: 10px 55px 10px 15px;
  font-size: 1em;
  font-weight: 400;
  color: #eee;
  cursor: pointer;
  background: #009688;
  transition: all .2s linear 0;
}

.reviews .items .item .accordion .title_block:before {
  content: '';
  position: absolute;
  display: block;
  width: 8px;
  height: 8px;
  right: 20px;
  top: 18px;
  border: 2px solid #fefefe;
  border-right-width: 0;
  border-top-width: 0;
  transform: rotate(-45deg);
}

.reviews .items .item .accordion .title_block:hover {
  background: #26A69A;
}

.reviews .items .item .accordion .active_block .title_block {
  background: #26A69A;
  color: #fefefe;
}

.reviews .items .item .accordion .active_block .title_block:before {
  border: 2px solid #fefefe;
  border-left-width: 0;
  border-bottom-width: 0;
  top: 18px;
}

.reviews .items .item .accordion .info {
  display: none;
  padding: 10px 15px;
  overflow: hidden;
  background: #f7f7f7;
}

.reviews .items .item .accordion .info img {
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
}

.reviews .items .item .accordion .info p {
  margin: 0.5em 0;
  font-size: .9em;
  text-indent: .5em;
}

.reviews .items .item .accordion .info .img-left {
  width: 25%;
  float: left;
  margin: 5px 15px 5px 0;
}

.reviews .items .item .accordion .info .img-right {
  width: 25%;
  float: right;
  margin: 5px 0 5px 15px;
}

/* ======================================
 * ПОДВАЛ (footer, contacts)
/* ====================================== */

/*  блок подвала */

footer {
  background: url("../img/bg/footer-bg.jpg") 100% 100% no-repeat;
  background-attachment: fixed;
  background-size: cover;
}

footer .contacts {
  padding: 3em 0;
  color: white;
  background: rgba(0, 0, 0, 0.7);
}

footer .contacts h2 {
  color: white;
}

footer .contacts h3 {
  margin: 2em auto 0.5em;
  padding: 0.5em;
  text-align: center;
  font-size: 1.1em;
  font-weight: bold;
  color: white;
  border-bottom: 1px solid rgba(255, 255, 255, 0.5);
}

footer .contacts h4 {
  margin: 2em auto 1em;
  padding: 1em 0;
  text-align: center;
  font-style: italic;
  border-top: 1px dashed rgba(255, 255, 255, 0.5);
  border-bottom: 1px dashed rgba(255, 255, 255, 0.5);
}

footer .contacts .items {
  display: flex;
  flex-wrap: wrap;
  margin-left: -1.15%;
  margin-right: -1.15%;
  justify-content: space-around;
  align-items: flex-start;
}

footer .contacts .items .item {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 1.15%;
  margin-right: 1.15%;
  width: 31.03333333%;
  align-items: center;
}

footer .contacts .items .item h3 {
  text-align: left;
}

footer .contacts .items .item i {
  vertical-align: baseline;
}

footer .contacts .items .item p {
  margin: 0.5em 0;
}

footer .contacts .items .item .social-bottom a {
  text-decoration: none;
}

footer .contacts .items .item .social-bottom img {
  margin: 0 0.5em;
  background: #ffffff;
  border-radius: 50%;
  -moz-border-radius: 50%;
  -webkit-border-radius: 50%;
  -o-border-radius: 50%;
  -ms-border-radius: 50%;
}

footer .contacts .items .item .social-bottom img:hover {
  cursor: pointer;
  transform: scale(1.2);
  transition: all 1s linear;
}

footer .contacts #copyright {
  padding: 1.5em 0;
  text-align: center;
}

footer .contacts #copyright .copy-title {
  color: white;
}

footer .contacts #copyright .copy-title:hover {
  color: #4169e1;
}

footer .contacts #copyright a {
  text-decoration: none;
}

/* ======================================
 * СТРАНИЦА ОШИБКИ 404 (page 404)
/* ====================================== */

.error404 {
  margin: 1.5em auto;
  text-align: center;
}

.error404 .error-img {
  margin: 1em auto;
}

.error404 a {
  text-decoration: none;
}

@media screen and (max-width: 1200px) {
  .wrapper {
    padding-left: 30px;
    padding-right: 30px;
  }
}

@media screen and (max-width: 992px) {
  .wrapper {
    padding-left: 20px;
    padding-right: 20px;
  }

  #top-nav .nav-logo {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: rgba(65, 105, 225, 0.7);
  }

  #top-nav .nav-logo .fa {
    margin: 0 0.5em;
  }

  #top-nav .nav-logo span {
    margin: 0 1em;
  }

  #top-nav .nav-list {
    flex-flow: column wrap;
    /* .adaptive {
				flex-flow: column wrap;
			} */
  }

  #top-nav .nav-list li {
    display: none;
    border-bottom: 1px solid #D3D3D3;
    width: 100%;
  }

  #top-nav .nav-list li:first-child {
    border-top: none;
  }

  #top-nav .nav-list li:last-child {
    border-bottom: none;
  }

  header .top-line .logotip,
