  }

  .advantages .items .item {
    width: 47.7%;
  }

  .diagnostics .items .item {
    width: 81.03333333%;
  }

  .order-process .items .item {
    width: 39.36666667%;
  }

  .examples .items .item {
    width: 64.36666667%;
  }

  footer .contacts .items .item {
    width: 64.36666667%;
  }
}

